
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

.postcontainer {
    display: flex;
    flex-wrap: wrap;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 10px;
}

.namecontainer,
.agecontainer,
.gendercontainer,
.addresscontainer,
.addressplace {
    margin-bottom: 10px;
}

input[type="text"],
input[type="number"] {
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 3px;
    width: 150px;
    margin: 10px;
}
.header{
    color:white;
    text-shadow: 0.5px 0.5px 1.9px black;
    font-weight: 500;
}

.gendercontainer label {
    display: inline-block;
    margin-right: 10px;
    margin-top: 10px;
}

.gendercontainer input[type="radio"] {
    margin-right: 5px;
}
.idcontainer input{
    cursor:not-allowed;
}
table {
    border-collapse: collapse;
    /* Combine table borders */
    width: 100%;
    /* Make table full width */
    text-align: left;
    /* Align cell content to the left */
}

th,
td {
    border: 1px solid #ddd;
    padding: 8px;
}

th {
    background-color: #f2f2f2;
}

td:nth-child(even) {
    background-color: #f9f9f9;
}

.address {
    line-height: 1.5;
}
button{
    background-color: #2ECC71;
    color:white;
    width: 60px;
    height: 30px;
    border: none;
    margin-left: 10px;
    margin-top: 8px;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s ease-in;

}
button:hover{

    background-color: #28b630;

}
.searchbars input{
    width:70%;
    border: 0.6px solid black;

    
}
 